header {
	background-color: #fff;
	background-image: url($white-bg-image);
	background-repeat: repeat;
	background-attachment: fixed;
	padding: em(5px) 0;
	position: relative;
}
header .holder {
	padding-bottom: 0;
}
header .site-title {
	margin-top: 0;
	text-transform: uppercase;
	font-size: em(80px);
	float: left;
}
header .site-title,
header .site-title a {
	border-bottom-width: 0;
	color: $primary-color;
	display: block;
}
header .site-title a {
	border-radius: em(10px);
	padding: 0 em(3px);
}
header .site-title a:hover,
header .site-title a:focus {
	background-color: $light-accent-color;
	background-color: $site-title-bg-color;
	color: $site-title-color;
}
header .childs-current-age {
	color: $childs-current-age-color;
	float: right;
	font-size: em(25px);
	margin-top: em(40px, 25px);
	font-family: $headline-font;
}
@media(max-width: em(1000px) ) {
	header {
		font-size: 75%;
	}
}
@media (max-width: em(780px) ) {
	header {
		font-size: 70%;
	}
}
@media (max-width: em(700px) ) {
	header {
		font-size: 65%;
	}
}
@media (max-width: em(640px) ) {
	header {
		font-size: 100%;
	}
	header .site-title,
	header .childs-current-age {
		float: none;
		text-align: left;
	}
	header .childs-current-age {
		padding-left: em(10px);
		margin-top: 0;
		text-align: left;
		font-size: em(15px);
	}
	header .site-title {
		font-size: em(30px);
	}
	header .site-title a {
		display: inline-block;
	}
}
